@include font(futuraBold, FuturaPTBold);
@include font(futuraDemi, FuturaPTDemi);
@include font(futuraHeavy, FuturaPTHeavy);
@include font(futuraMedium, FuturaPTMedium);
@include font(helvetica, HelveticaNeue);

@mixin fb {
	font-family: futuraBold; }

@mixin fd {
	font-family: futuraDemi; }

@mixin fh {
	font-family: futuraHeavy; }

@mixin fm {
	font-family: futuraMedium; }

@mixin helv {
	font-family: helvetica; }
