}

.btn {
  color: #fff;
  background-color: transparent;
  font-family: helvetica;
  font-weight: 700;
}

.btn_hover:hover, .btn .btn.is-active {
  box-shadow: 0 0 10px 2px #45018a;
  border-radius: 3px;
  border: 2px dashed #ec094b;
}

.btn_header {
  padding: 11px 28px;
  font-size: 13px;
  line-height: 1.23;
  letter-spacing: 0.2px;
  border: 2px solid rgba(69, 1, 138, 0.7);
  border-radius: 3px;
}

.btn_nav-secondary {
  padding: 10px 25px 12px;
  border: 2px solid transparent;
}

.btn_nav-secondary:hover, .btn_nav-secondary.is-active {
  color: rgba(243, 195, 4, 0.8);
  background-image: linear-gradient(180deg, #ffd100 0%, #ff7300 100%);
  background-clip: text;
  -webkit-background-clip: text;
}

.btn_game-item {
  width: 80px;
  height: 46px;
  box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.2), inset 0 0 7px 1px #ae5cff;
  border-radius: 3px;
  background-color: #8000ff;
  background: linear-gradient(to bottom, #c16cf3 0%, #8300ff 49%, #5600ff 52%, #4a08ff 92%, #7a30ff 100%);
  text-shadow: 0 2px 0 #b61045;
  color: #ffffff;
  font-family: futuraBold;
  font-size: 15px;
  text-transform: uppercase;
  letter-spacing: -0.47px;
}

.btn_game-item:hover {
  background-color: #e21d5c;
  box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.2), inset 0 0 7px 1px #ff8bb0;
  background: linear-gradient(to bottom, #f9629f 0%, #e21d5d 49%, #d30e2e 50%, #d71939 89%, #ea4f72 100%);
}

.btn_winners {
  color: #ffffff;
  font-family: helvetica;
  font-size: 13px;
  line-height: 1.23;
  letter-spacing: -0.24px;
  background-color: transparent;
  width: 80px;
  text-align: left;
  position: relative;
}

.btn_winners:hover, .btn_winners.is-active {
  text-shadow: 1px 0px 2px #fff;
}

.btn_winners:hover:after, .btn_winners.is-active:after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 83px;
  height: 3px;
  box-shadow: 0 0 5px #e21d5c;
  border-radius: 1px;
  background-color: #e21d5c;
  background-image: linear-gradient(to top, rgba(255, 255, 255, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%);
}

.btn_registr {
  color: #ffffff;
  font-family: futuraDemi;
  font-size: 19px;
  letter-spacing: 0px;
  padding: 7px 55px 13px 52px;
  border-radius: 3px;
  border: 3px solid #e21d5c;
  background-color: transparent;
  box-sizing: border-box;
}

.layout {
