  position: absolute;
  transform: translateX(-150%);
}

.navigation__menu {
  display: none;
}

@media only screen and (max-width: 710px) {
  .navigation__menu {
    display: inline-block;
  }
}

.navigation__list {
  font-family: futuraDemi;
  font-size: 18px;
  text-transform: uppercase;
  display: flex;
  margin-left: -15px;
  justify-content: center;
}

@media only screen and (max-width: 710px) {
  .navigation__list {
    display: none;
  }
}

.navigation__list li {
  position: relative;
  margin-right: 14px;
  border-radius: 8px;
  display: block;
}

.navigation__list li:last-child {
  margin-right: 0;
}

.navigation__list li:before {
  content: "";
  position: absolute;
  top: 50%;
  margin-top: -2px;
  width: 4px;
  height: 4px;
  left: -10px;
  box-shadow: 0 0 10px 2px #e21d5c;
  border: 2px solid #ffa400;
  border-radius: 50%;
}

.navigation__list li:first-child:before {
  display: none;
}

.navigation__list li a {
  text-decoration: none;
  color: #ffffff;
  font-family: futuraDemi;
  line-height: 16px;
  letter-spacing: -0.33px;
  position: relative;
  padding: 25px 19px;
  z-index: 150;
}

.navigation__list li a:hover, .navigation__list li a.is-active {
  background-image: linear-gradient(180deg, #ffd100 0%, #ff7300 100%);
  background-clip: text;
  -webkit-background-clip: text;
  text-shadow: 0 2px 2px rgba(0, 0, 0, 0.5);
  color: rgba(243, 195, 4, 0.4);
}

.navigation__list li a:hover:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  box-shadow: 0 0 5px #e21d5c;
  background-color: #e21d5c;
  background-image: linear-gradient(to top, rgba(255, 255, 255, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%);
  z-index: 3;
}

.navigation__list li a:hover:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: -10%;
  top: -20%;
  width: 120%;
  height: 140%;
  background: radial-gradient(ellipse at center, #9b0021 0%, rgba(153, 32, 56, 0.4) 50%, rgba(153, 32, 56, 0.2) 70%, transparent 90%, transparent 100%);
  z-index: -1;
}

.navigation__search {
  color: #8000ff;
  font-family: helvetica;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: -0.28px;
  font-weight: 700;
